<template>
	<div class="container">
		<v-back title="消息" />
		<div class="scroll">
			<div class="list" v-for="(item, index) in list" :key="index" @click="toList(item.person_entry_exit_id)">
				<div class="flex space-between align_center">
					<div class="flex align_center">
						<h1><span style="font-size: 4vw;">{{item.from_user_name}}</span>（{{typeText(item.from && item.from.usertype)}}）</h1>
						<md-icon name="switch" size="lg" style="margin: 0 1.5vw 0 1vw;" color="#1acfc6"></md-icon>
						<h1><span style="font-size: 4vw;">{{item.to_user_name}}</span>（{{typeText(item.from && item.to.usertype)}}）</h1>
					</div>
					<div class="flex un_read">
						<span v-if="unReadCount(item.list) != 0" class="icon">{{unReadCount(item.list)}}</span>
					</div>
				</div>
				<div class="flex align_center space-between" style="padding: 1vw 0;">
					<h1>最新消息：{{item.content}}</h1>
					<md-icon name="arrow-right" size="md" style="margin: 0 1vw;"></md-icon>
				</div>
				
				<h1 style="color: rgba(0,0,0,.5);">{{item.send_time}}</h1>
			</div>
			<div v-if="list.length == 0" class="no_data" style="padding-top: 10vw;">
			    <md-result-page text="暂无消息"></md-result-page>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'news_index',
		data() {
			return {
				list: [],
				user: JSON.parse(localStorage.getItem('user'))
			}
		},
		methods: {
			toList(id) {
				this.$router.push('/news/detail?id=' + id)
			},
			getList() {
				this.$getL('/news/list', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
					let list = res.data
					this.list = list
					this.$toast.hide()
				})
			},
			typeText(usertype) {
				if (usertype == 'S') {
					return '学生'
				} else if (usertype == 'T') {
					return '老师'
				} else if (usertype == 'V') {
					return '访客'
				} else if (usertype == 'Y') {
					return '黄名单人员'
				} else if (usertype == 'U') {
					return '系统管理员'
				} else if (usertype == 'I') {
					return '陌生人'
				} else {
					return '其他'
				}
			},
			unReadCount(list) {
				let count = 0
				//这里要加一层判断 to_user_nbr 是否与登录者相同 才能触发未读变已读
				list.forEach(item => {
					if (item.read_flag == '0' && item.to_user_id == this.user.user_id) {
						count++
					}
				})
				return count;
			}
		},
		mounted() {
			this.getList();
		}
	}
</script>

<style scoped="scoped">
	.scroll {
		height: calc(100vh - 14vw);
		overflow-y: auto;
	}
	.list {
		box-sizing: border-box;
		margin: 2vw;
		padding: 4vw;
		background-color: #fff;
	}
	.un_read {
		color: red;
		font-size: 3.2vw;
	}
	.un_read .icon {
		display: block;
		color: #fff;
		background-color: red;
		border-radius: 2vw;
		min-width: 2vw;
		padding: 0 1vw;
		height: 4vw;
		line-height: 4vw;
		text-align: center;
	}
	.is_read {
		color: #ccc;
	}
</style>
